const getMarqueeList = () => {
  const marqueeList = [];
  const itemsTotal = marqueeImages.length - 1; 

  const divideByFour = itemsTotal/4;
  const itemsOffset = itemsTotal%20;
  const itemsIndex = [ divideByFour, divideByFour*2, divideByFour*3, (divideByFour*4)+itemsOffset ];

  for(let i=0; i<marqueeImages.length; i+=1){
    marqueeList.push(marqueeImages[i].default);
  }

  const firstArray = marqueeList.slice(0, itemsIndex[0]);
  const secondArray = marqueeList.slice(itemsIndex[0]+1, itemsIndex[1]);
  const thirdArray = marqueeList.slice(itemsIndex[1]+1, itemsIndex[2]);
  const fourthArray = marqueeList.slice(itemsIndex[2]+1, itemsIndex[3]);

  return { first: firstArray, second: secondArray, third: thirdArray, fourth: fourthArray };
}


